import { getDefaultState } from ".";

export default {
  SET_DEFAULT_STATE(state) {
    localStorage.clear();
    Object.assign(state, getDefaultState());
  },
  SET_TOKEN(state, token) {
    localStorage.setItem("tithonusToken", token);
    state.token = token;
  },
  SET_REPOS(state, repos) {
    state.repos = repos;
  },
  SET_USER(state, user) {
    state.user = user;
  },
  SET_PRS(state, prs) {
    state.prs = prs;
  },
  SET_BRANCHES(state, branches) {
    state.branches = branches;
  },
  SET_REPOUSERS(state, repoUsers) {
    state.repoUsers = repoUsers;
  },
  SET_ROLE(state, role) {
    localStorage.setItem("tithonusRole", role);
    state.role = role;
  },
  SET_ACTIVE_PR(state, pr) {
    state.activePr = pr;
  },
  SET_REVIEW(state, review) {
    state.review = review;
  },
  SET_PAGINATION_LENGTH(state, paginationLength) {
    state.paginationLength = paginationLength;
  },
  SET_PAGINATED_REPOS(state, paginatedRepos) {
    state.paginatedRepos = paginatedRepos;
  },
  SET_REPO_SEARCH_TERM(state, term) {
    state.repoSearchTerm = term;
  },
  SET_PAGINATED_PRS(state, paginatedPrs) {
    state.paginatedPrs = paginatedPrs;
  },
  SET_PR_SEARCH_TERM(state, term) {
    state.prSearchTerm = term;
  },
  SET_REPO_CONFIG(state, repoConfig) {
    state.repoConfig = repoConfig;
  },
};
