<template>
  <div class="loader" :class="{ 'loader--visible': visible }">
    <span class="text">Loading...</span>
  </div>
</template>
<script>
export default {
  name: "loader",
  props: ["visible"],
};
</script>
<style lang="scss">
.text {
  color: #fff;
  font-size: 32px !important;
  font-weight: 600;
}
.loader {
  background: #0d1117;
  transition: 0.15s ease-in opacity;
  position: absolute;
  height: 100vh;
  width: 100vw;
  pointer-events: none;
  opacity: 0;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
  &--visible {
    opacity: 1;
    pointer-events: all;
  }
}
</style>
