import { Octokit } from "octokit";
import { RequestInterface } from "@octokit/types";
import { Query, RequestParameters } from "@octokit/graphql/dist-types/types";
import { errorInterceptor } from "./interceptors";
import { GraphqlResponseError } from "@octokit/graphql";

const actionScope = `loader`;

export default class OctokitWrapper {
  octokit!: Octokit;

  constructor(token: string, private dispatch: any) {
    this.octokit = new Octokit({ auth: token });
  }

  async paginate<R extends RequestInterface>(request: R, parameters?: Parameters<R>[0]) {
    return this.wrapRequest(() => this.octokit.paginate(request, parameters));
  }

  async graphql(query: Query, variables: RequestParameters) {
    return this.wrapRequest(() => this.octokit.graphql(query, variables));
  }

  async wrapRequest(request: () => Promise<any>) {
    this.dispatch(`${actionScope}/incrementRequests`);
    try {
      return await request();
    } catch (error) {
      if (error instanceof GraphqlResponseError) {
        if (error.errors?.some((err) => err.type === "FORBIDDEN")) {
          await errorInterceptor({ status: 403 });
        } else {
          throw error;
        }
      } else {
        await errorInterceptor(error);
      }
    } finally {
      this.dispatch(`${actionScope}/decrementRequests`);
    }
  }
}
