import store from "../store";
import router from "@/router";

export const responseInterceptor = (response) => response;

export const errorInterceptor = async (error) => {
  try {
    const { status } = error;
    if (status === 401) {
      await store.dispatch("logout");
      return;
    }
    if (status === 403) {
      alert("Tithonus is not installed properly in this repository");
      await router.push("/repo-list");
      return;
    }
  } catch (e) {
    // Ignore any errors here
  }
  throw error;
};
