import Vue from "vue";
import App from "./App.vue";
import vuetify from "./plugins/vuetify";
import "./plugins/apexcharts";
import "highlight.js/styles/github-dark.css";
import router from "./router";
import store from "./store";
import { setupInterceptors } from "./utils/httpInterceptors";
import "./router/routing";

// @ts-ignore
import VueInputAutoWidth from "vue-input-autowidth";

Vue.use(VueInputAutoWidth);
Vue.directive("visible", function (el, binding) {
  el.style.visibility = binding.value ? "visible" : "hidden";
});
Vue.mixin({
  methods: {
    formattedTooltipText(textForTooltip) {
      let text = "<div class='d-flex flex-column'>";
      let newLineIndex;
      let helpText = textForTooltip;
      do {
        newLineIndex = helpText.indexOf("\n");
        if (newLineIndex === -1) text += `<span>${helpText.substring(0, helpText.length)}</span>`;
        else text += `<span class="mb-n1">${helpText.substring(0, newLineIndex)}</span>`;
        helpText = helpText.substring(newLineIndex + 2, helpText.length);
      } while (newLineIndex !== -1);
      text += "</div>";
      return text;
    },
  },
});

new Vue({
  vuetify,
  router,
  created() {
    setupInterceptors(store);
  },
  store,
  render: (h) => h(App),
}).$mount("#app");
