import router from "./index";
import store from "../store";

router.beforeEach(async (to, from, next) => {
  if (to.name === "PullRequestConfirmation" && from.name === "PullRequestDetails") {
    history.replaceState(null, "", from.path);
    return;
  }
  let pushTo = () => next();
  if (from.name === "PullRequestConfirmation" && to.name === "AddReview") {
    if (confirm("You will be routed back to the Pull Request details page and your review will be automatically approved. Are you sure you want to continue?")) {
      await store.dispatch("submitReview", {
        repoUrl: from.params.repoUrl,
        pullNumber: from.params.pullNumber,
        reviewNumber: store.getters.review.databaseId,
        event: "APPROVE", // can be made request changes too
      });
      history.replaceState(null, "", `/${from.params.repoUrl}/pulls`);
      pushTo = async () => router.push({ path: `/${from.params.repoUrl}/pulls/${from.params.pullNumber}` });
    } else {
      history.pushState(null, "", from.path);
      return;
    }
  }
  store.dispatch("loader/show");
  setTimeout(() => {
    pushTo();
  }, 150);
});

router.afterEach(() => {
  const interval = setInterval(() => {
    if (store.getters["loader/pendingRequests"] <= 0) {
      store.dispatch("loader/hide");
      clearInterval(interval);
    }
  }, 200);
});
