// @ts-ignore
import gql from "fake-tag";

export const UPDATE_ISSUE_COMMENT = gql`
  mutation updateIssueComment($id: ID!, $body: String!) {
    updateIssueComment(input: { id: $id, body: $body }) {
      clientMutationId
    }
  }
`;

export const DELETE_ISSUE_COMMENT = gql`
  mutation deleteIssueComment($id: ID!) {
    deleteIssueComment(input: { id: $id }) {
      clientMutationId
    }
  }
`;

export const ADD_PULL_REQUEST_REVIEW_COMMENT = gql`
  mutation addPullRequestReviewComment($body: String!, $pullRequestReviewId: ID, $path: String, $position: Int, $inReplyTo: ID) {
    addPullRequestReviewComment(input: { body: $body, pullRequestReviewId: $pullRequestReviewId, path: $path, position: $position, inReplyTo: $inReplyTo }) {
      comment {
        databaseId
      }
    }
  }
`;

export const UPDATE_PULL_REQUEST_REVIEW_COMMENT = gql`
  mutation updatePullRequestReviewComment($id: ID!, $body: String!) {
    updatePullRequestReviewComment(input: { pullRequestReviewCommentId: $id, body: $body }) {
      clientMutationId
    }
  }
`;

export const DELETE_PULL_REQUEST_REVIEW_COMMENT = gql`
  mutation deletePullRequestReviewComment($id: ID!) {
    deletePullRequestReviewComment(input: { id: $id }) {
      clientMutationId
    }
  }
`;

export const GET_ASSIGNABLE_REVIEWERS = gql`
  query repository($name: String!, $owner: String!) {
    repository(name: $name, owner: $owner) {
      assignableUsers(first: 100) {
        nodes {
          login
          avatarUrl
        }
      }
    }
  }
`;

export const GET_PULL_REQUEST = gql`
  query getPullRequest(
    $repoName: String!
    $repoOwner: String!
    $pullNumber: Int!
    $userLogin: String!
    $getReviewRequests: Boolean = false
    $getViewerLatestPendingReviews: Boolean = false
    $getViewerLatestReview: Boolean = false
    $getLatestReviews: Boolean = false
    $getTimelineItems: Boolean = false
    $getReviewThreads: Boolean = false
  ) {
    repository(name: $repoName, owner: $repoOwner) {
      pullRequest(number: $pullNumber) {
        number
        headRefName
        headRefOid
        baseRefName
        title
        author {
          ...userFields
        }
        state
        merged
        commits {
          totalCount
        }
        mergeable
        bodyHTML
        createdAt
        viewerDidAuthor
        reviewDecision
        reviewRequests(last: 30) @include(if: $getReviewRequests) {
          nodes {
            ...reviewRequestFields
          }
        }
        viewerLatestReviewRequest {
          ...reviewRequestFields
        }
        viewerLatestPendingReviews: reviews(first: 1, author: $userLogin, states: PENDING) @include(if: $getViewerLatestPendingReviews) {
          nodes {
            ...reviewWithCommentFields
          }
        }
        viewerLatestReviews: reviews(last: 1, author: $userLogin, states: [APPROVED, CHANGES_REQUESTED]) @include(if: $getViewerLatestReview) {
          nodes {
            ...reviewWithCommentFields
          }
        }
        latestReview: reviews(last: 1, states: [APPROVED, CHANGES_REQUESTED, PENDING]) @include(if: $getLatestReviews) {
          nodes {
            ...reviewWithCommentFields
          }
        }
        latestReviews(last: 30) @include(if: $getLatestReviews) {
          nodes {
            ...reviewFields
          }
        }
        timelineItems(last: 100) @include(if: $getTimelineItems) {
          nodes {
            ...timelineItemFields
          }
        }
        reviewThreads(last: 100) @include(if: $getReviewThreads) {
          nodes {
            path
            viewerCanReply
            originalLine
            comments(last: 30) {
              nodes {
                ...reviewCommentFields
              }
            }
          }
        }
      }
    }
  }

  fragment userFields on User {
    avatarUrl
    login
  }

  fragment reviewRequestFields on ReviewRequest {
    __typename
    id
    databaseId
    requestedReviewer {
      ...userFields
    }
  }

  fragment reviewFields on PullRequestReview {
    __typename
    id
    databaseId
    author {
      ...userFields
    }
    state
    submittedAt
    bodyHTML
    commit {
      oid
    }
  }

  fragment reviewWithCommentFields on PullRequestReview {
    ...reviewFields
    comments(last: 30) {
      nodes {
        ...reviewCommentFields
      }
    }
  }

  fragment commentFields on Comment {
    id
    author {
      ...userFields
    }
    body
    bodyHTML
    createdAt
    publishedAt
    updatedAt
    viewerDidAuthor
    editor {
      ...userFields
    }
  }

  fragment reviewCommentFields on PullRequestReviewComment {
    ...commentFields
    databaseId
    diffHunk
    path
    originalPosition
    position
    viewerCanUpdate
    viewerCanDelete
    replyTo {
      id
      author {
        ...userFields
      }
    }
  }

  fragment timelineItemFields on PullRequestTimelineItems {
    __typename
    ... on Node {
      id
    }
    ... on PullRequestCommit {
      prCommit: commit {
        abbreviatedOid
        message
        author {
          user {
            ...userFields
          }
        }
      }
    }
    ... on ReviewRequestedEvent {
      actor {
        ...userFields
      }
      requestedReviewer {
        ...userFields
      }
      createdAt
    }
    ... on ReviewRequestRemovedEvent {
      actor {
        ...userFields
      }
      requestedReviewer {
        ...userFields
      }
      createdAt
    }
    ...reviewWithCommentFields
    ...commentFields
    ... on Updatable {
      viewerCanUpdate
    }
    ... on Deletable {
      viewerCanDelete
    }
    ... on RenamedTitleEvent {
      actor {
        ...userFields
      }
      createdAt
      currentTitle
      previousTitle
      subject {
        __typename
      }
    }
    ... on MergedEvent {
      actor {
        ...userFields
      }
      mergeRefName
      commit {
        abbreviatedOid
      }
      createdAt
    }
    ... on ClosedEvent {
      actor {
        ...userFields
      }
      createdAt
      id
    }
    ... on HeadRefDeletedEvent {
      actor {
        ...userFields
      }
      createdAt
      headRefName
    }
  }
`;
