import Vue from "vue";
import VueRouter, { RouteConfig } from "vue-router";

Vue.use(VueRouter);

const routes: RouteConfig[] = [
  {
    path: "/",
    name: "Redirect",
    component: () => import("../views/Redirect.vue"),
  },
  {
    path: "/repo-list",
    name: "Repository List",
    component: () => import("../views/RepositoryList.vue"),
  },
  {
    path: "/:repoUrl(.*)/pulls/:pullNumber",
    name: "PullRequestDetails",
    component: () => import("../views/PullRequestDetails.vue"),
  },
  {
    path: "/:repoUrl(.*)/pulls/:pullNumber/pr-confirmation",
    name: "PullRequestConfirmation",
    component: () => import("../views/PullRequestConfirmation.vue"),
  },
  {
    path: "/:repoUrl(.*)/pulls",
    name: "Pull Requests",
    component: () => import("../views/PullRequests.vue"),
  },
  {
    path: "/:repoUrl(.*)/configurations",
    name: "Configurations",
    component: () => import("../views/Configurations.vue"),
  },
  {
    path: "/:repoUrl(.*)/pulls/:pullNumber/bug-injection",
    name: "NewPullRequest",
    component: () => import("../views/NewPullRequest.vue"),
  },
  {
    path: "/:repoUrl(.*)/pulls/:pullNumber/add-review",
    name: "AddReview",
    component: () => import("../views/AddReview.vue"),
  },
  {
    path: "/:repoUrl(.*)/compare",
    name: "BranchDifference",
    component: () => import("../views/BranchDifference.vue"),
  },
  {
    path: "/:repoUrl(.*)/reviews",
    name: "Reviews",
    component: () => import("../views/Reviews.vue"),
  },
  {
    path: "/:repoUrl(.*)/reviewer-tracking/:username/performance-report",
    name: "Performance Report",
    component: () => import("../views/PerformanceReport.vue"),
  },
  {
    path: "/:repoUrl(.*)/reviewer-tracking",
    name: "Reviewer Tracking",
    component: () => import("../views/ReviewerTracking.vue"),
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.NODE_ENV === "production" ? process.env.VUE_APP_PROD_BASE_URL : "/",
  routes,
});

export default router;
