import store from "../store";
import { CustomPullRequest, ReviewScore } from "@/types/GraphQL";
import { PullRequestStatus } from "@/types/PullRequestStatus";

export default async function (
  repoUrl: string,
  pullRequest: CustomPullRequest,
  getReviewRequests: boolean,
  getViewerLatestPendingReviews: boolean,
  getViewerLatestReview: boolean,
  getLatestReviews: boolean,
  getTimelineItems: boolean,
  getReviewThreads: boolean
) {
  //TODO - Store updatable fields separately in the store - Ziya
  const oldActivePr: CustomPullRequest | undefined = store.getters.activePr;
  if (oldActivePr) {
    if (!getReviewRequests) {
      pullRequest.reviewRequests = oldActivePr.reviewRequests;
    }
    if (!getViewerLatestPendingReviews) {
      pullRequest.viewerLatestPendingReviews = oldActivePr.viewerLatestPendingReviews;
    }
    if (!getViewerLatestReview) {
      pullRequest.viewerLatestReviews = oldActivePr.viewerLatestReviews;
      pullRequest.viewerLatestReviewScore = oldActivePr.viewerLatestReviewScore;
      pullRequest.viewerCanSeeInjectedBugs = oldActivePr.viewerCanSeeInjectedBugs;
    }
    if (!getLatestReviews) {
      pullRequest.latestReviews = oldActivePr.latestReviews;
      pullRequest.latestReview = oldActivePr.latestReview;
      pullRequest.latestReviewScore = oldActivePr.latestReviewScore;
      pullRequest.reviewRequired = oldActivePr.reviewRequired;
    }
    if (!getTimelineItems) {
      pullRequest.timelineItems = oldActivePr.timelineItems;
    }
    if (!getReviewThreads) {
      pullRequest.reviewThreads = oldActivePr.reviewThreads;
      pullRequest.parsedReviewThreads = oldActivePr.parsedReviewThreads;
    }
  }

  if (pullRequest.reviewThreads?.nodes) {
    pullRequest.parsedReviewThreads = {};
    pullRequest.reviewThreads.nodes.forEach((reviewThread) => {
      const comments = reviewThread?.comments?.nodes;
      if (!comments || comments.length == 0 || !comments[0]) {
        return;
      }
      pullRequest.parsedReviewThreads[comments[0].id] = comments;
    });
  }

  if (getViewerLatestReview) {
    pullRequest.viewerCanSeeInjectedBugs = store.getters.user.login === pullRequest.author?.login || store.getters.role === "admin";
    if (pullRequest.viewerLatestReviews?.nodes?.length) {
      const [viewerLatestReviewScore]: ReviewScore[] = await store.dispatch("getReviewScores", {
        repoUrl,
        pullNumber: pullRequest.number,
        reviewIds: [pullRequest.viewerLatestReviews.nodes[0].databaseId],
      });
      pullRequest.viewerLatestReviewScore = viewerLatestReviewScore;
      if (!viewerLatestReviewScore || !viewerLatestReviewScore.faulty) {
        pullRequest.viewerCanSeeInjectedBugs = true;
      }
    } else {
      pullRequest.viewerLatestReviewScore = { faulty: false };
    }
  }

  const latestReview = pullRequest.latestReview?.nodes?.[0];
  if (getLatestReviews) {
    if (latestReview?.databaseId) {
      const [latestReviewScore]: ReviewScore[] = await store.dispatch("getReviewScores", {
        repoUrl,
        pullNumber: pullRequest.number,
        reviewIds: [latestReview.databaseId],
      });
      pullRequest.latestReviewScore = latestReviewScore;
    } else {
      pullRequest.viewerLatestReviewScore = { faulty: false };
    }

    pullRequest.reviewRequired = latestReview?.state !== "APPROVED" || pullRequest.latestReviewScore?.faulty === true || pullRequest.headRefOid !== latestReview?.commit?.oid;
  }

  if (getLatestReviews || getViewerLatestPendingReviews) {
    if (pullRequest.viewerLatestPendingReviews?.nodes && pullRequest.viewerLatestPendingReviews.nodes.length > 0) {
      store.commit("SET_REVIEW", pullRequest.viewerLatestPendingReviews.nodes[0]);
    } else if (latestReview) {
      store.commit("SET_REVIEW", latestReview);
    } else {
      store.commit("SET_REVIEW", undefined);
    }
  }

  pullRequest.status = getPullRequestStatus(pullRequest);
  store.commit("SET_ACTIVE_PR", pullRequest);
}

function getPullRequestStatus(pullRequest: CustomPullRequest): PullRequestStatus {
  if (pullRequest.merged) {
    return {
      statusIcon: "GREEN",
      statusTitle: "Pull request successfully merged",
    };
  }

  // Check PR mergeability
  let defaultStatus: Partial<PullRequestStatus>;
  if (pullRequest.mergeable === "MERGEABLE") {
    defaultStatus = { conflictIcon: "GREEN", conflictTitle: "This branch has no conflicts with the base branch" };
  } else if (pullRequest.mergeable === "CONFLICTING") {
    defaultStatus = {
      conflictIcon: "RED",
      conflictTitle: "This branch has conflicts with the base branch",
      conflictTitleColor: "#ba2501",
      enableMergeButton: false,
    };
  } else {
    defaultStatus = {
      conflictIcon: "YELLOW",
      conflictTitle: "Checking if this pull request can be merged...",
      conflictTitleColor: "#ffc501",
      enableMergeButton: false,
    };
  }

  // Other status details
  if (pullRequest.latestReviewScore?.faulty && !pullRequest.reviewRequests?.nodes?.length) {
    defaultStatus.statusIcon = "RED";
    defaultStatus.statusTitle = "Review was faulty. Waiting for an admin action...";
    defaultStatus.faulty = true;
    defaultStatus.statusTitleColor = "#ba2501";
    if (store.getters.role === "admin") {
      defaultStatus.statusSubtitle = "Reassign this pull request to the same or another reviewer";
    }
    return defaultStatus as PullRequestStatus;
  }

  if (pullRequest.latestReview?.nodes?.[0]?.state === "CHANGES_REQUESTED" && !pullRequest.reviewRequests?.nodes?.length && !pullRequest.latestReviewScore?.faulty) {
    defaultStatus.statusIcon = "ORANGE";
    defaultStatus.statusTitle = "Changes Requested";
    defaultStatus.statusSubtitle = `Add more commits by pushing to ${pullRequest.headRefName}`;
    defaultStatus.statusTitleColor = "#c85b03";
    return defaultStatus as PullRequestStatus;
  }

  if (pullRequest.viewerLatestReviewRequest && store.getters.role === "reviewer") {
    defaultStatus.showAddReviewButton = true;
  }
  if (pullRequest.reviewRequired) {
    defaultStatus.statusIcon = "ORANGE";
    defaultStatus.statusTitle = "Review Required";
    defaultStatus.statusSubtitle = "At least one approved review is required for the most recent commit";
    defaultStatus.statusTitleColor = "#c85b03";

    return defaultStatus as PullRequestStatus;
  }

  defaultStatus.statusIcon = "GREEN";
  defaultStatus.statusTitle = "No Additional Review Required";
  if (defaultStatus.enableMergeButton !== false) {
    defaultStatus.enableMergeButton = true;
  }
  defaultStatus.showMergeButtonRow = true;
  return defaultStatus as PullRequestStatus;
}
