import Vue from "vue";
import Vuex from "vuex";
import actions from "./actions";
import mutations from "./mutations";
import getters from "./getters";
import { loader } from "./modules/loader";

Vue.use(Vuex);

export const getDefaultState = () => {
  return {
    token: localStorage.getItem("tithonusToken"),
    repos: [],
    user: undefined,
    prs: [],
    branches: [],
    role: localStorage.getItem("tithonusRole") ? localStorage.getItem("tithonusRole") : "author",
    activePr: undefined,
    repoUsers: [],
    review: undefined,
    paginationLength: 10,
    paginatedRepos: [],
    repoSearchTerm: "",
    paginatedPrs: [],
    prSearchTerm: "",
    repoConfig: { seeSelfPerformance: false, faultyThreshold: 75 },
  };
};

export default new Vuex.Store({
  state: getDefaultState(),
  mutations,
  actions,
  getters,
  modules: {
    loader,
  },
});
