<template>
  <v-app>
    <v-main class="route-container" :style="{ maxHeight: loading ? '100vh' : '', overflow: loading ? 'hidden' : '' }">
      <Loader :visible="loading || userDataLoading" />
      <router-view v-if="!userDataLoading" />
    </v-main>
  </v-app>
</template>

<script>
import Loader from "./components/Loader.vue";
import { mapState } from "vuex";
export default {
  name: "App",
  data: () => ({
    userDataLoading: true,
  }),
  components: { Loader },
  computed: {
    ...mapState("loader", ["loading"]),
  },
  async mounted() {
    try {
      await this.$store.dispatch("setAuthenticatedUserClients");
    } catch (error) {
      console.debug("Error loading user data:", error);
    }
    this.userDataLoading = false;
  },
};
</script>

<style lang="scss">
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#nav {
  padding: 30px;

  a {
    font-weight: bold;
    color: #2c3e50;

    &.router-link-exact-active {
      color: #42b983;
    }
  }
}
</style>
