export default {
  token(state) {
    return state.token;
  },
  repos(state) {
    return state.repos.filter((item) => {
      return item.repository.full_name.toLowerCase().includes(state.repoSearchTerm.toLowerCase());
    });
  },
  user(state) {
    return state.user;
  },
  role(state) {
    return state.role;
  },
  prs(state) {
    return state.prs.filter((item) => {
      return item.title.toLowerCase().includes(state.prSearchTerm.toLowerCase());
    });
  },
  branches(state) {
    return state.branches;
  },
  repoUsers(state) {
    return state.repoUsers;
  },
  activePr(state) {
    return state.activePr;
  },
  review(state) {
    return state.review;
  },
  paginationLength(state) {
    return state.paginationLength;
  },
  paginatedRepos(state) {
    return state.paginatedRepos;
  },
  paginatedPrs(state) {
    return state.paginatedPrs;
  },
  repoConfig(state) {
    return state.repoConfig;
  },
};
