export const loader = {
  namespaced: true,
  state: {
    loading: false,
    pendingRequests: 0,
  },
  actions: {
    show({ commit }) {
      commit("show");
    },
    hide({ commit }) {
      commit("hide");
    },
    incrementRequests({ commit }) {
      commit("incrementRequests");
    },
    decrementRequests({ commit }) {
      commit("decrementRequests");
    },
  },
  mutations: {
    show(state) {
      state.loading = true;
    },
    hide(state) {
      state.loading = false;
    },
    incrementRequests(state) {
      state.pendingRequests++;
    },
    decrementRequests(state) {
      state.pendingRequests--;
    },
  },
  getters: {
    pendingRequests(state) {
      return state.pendingRequests;
    },
  },
};
