import {
  ADD_PULL_REQUEST_REVIEW_COMMENT,
  DELETE_ISSUE_COMMENT,
  DELETE_PULL_REQUEST_REVIEW_COMMENT,
  GET_ASSIGNABLE_REVIEWERS,
  GET_PULL_REQUEST,
  UPDATE_ISSUE_COMMENT,
  UPDATE_PULL_REQUEST_REVIEW_COMMENT,
} from "../models/graphql";
import router from "@/router";
import httpClient from "@/utils/httpClient";
import OctokitWrapper from "@/utils/OctokitWrapper.ts";
import parsePullRequest from "@/utils/pullRequestParser";

const { VUE_APP_HTTP_BASE_URL, VUE_APP_HTTP_PORT, VUE_APP_HTTPS_BASE_URL, VUE_APP_HTTPS_PORT } = process.env;
let VUE_APP_BASE_URL = VUE_APP_HTTP_BASE_URL;
let VUE_APP_PORT = VUE_APP_HTTP_PORT;
if (location.protocol === "https:") {
  VUE_APP_BASE_URL = VUE_APP_HTTPS_BASE_URL;
  VUE_APP_PORT = VUE_APP_HTTPS_PORT;
}

let octokitWrapper;

export default {
  async setAuthenticatedUserClients({ getters, dispatch, commit }) {
    const { token } = getters;
    octokitWrapper = new OctokitWrapper(token, dispatch);
    httpClient.defaults.headers.common["Github-Token"] = token;

    if (token) {
      const authenticatedUserResponse = await octokitWrapper.wrapRequest(octokitWrapper.octokit.rest.users.getAuthenticated);
      await commit("SET_USER", authenticatedUserResponse.data);
    }
  },

  setRole({ commit }, role) {
    commit("SET_ROLE", role);
  },

  async authorize({ commit, dispatch }, code) {
    try {
      const response = await httpClient.get(`${VUE_APP_BASE_URL}:${VUE_APP_PORT}/auth`, { params: { code: code } });
      await commit("SET_TOKEN", response.data.token);
      await dispatch("setAuthenticatedUserClients");
    } catch (error) {
      console.error(error);
    }
  },

  async getUserRepositories({ commit }) {
    try {
      const response = await octokitWrapper.paginate(octokitWrapper.octokit.rest.repos.listForAuthenticatedUser, { per_page: 100 });
      const getRoles = function (repo) {
        const permissions = repo.permissions;
        return {
          admin: permissions.maintain || permissions.admin,
          author: permissions.triage && permissions.pull,
          reviewer: permissions.push,
        };
      };
      commit(
        "SET_REPOS",
        response.map((repo) => {
          return {
            repository: repo,
            roles: getRoles(repo),
          };
        })
      );
    } catch (error) {
      console.error(error);
    }
  },

  getPaginatedRepos({ getters, commit }, pageNo = 1) {
    let start = (pageNo - 1) * getters.paginationLength;
    let end = Math.min(pageNo * getters.paginationLength, getters.repos.length);
    commit("SET_PAGINATED_REPOS", getters.repos.slice(start, end));
  },

  async getRepoPullRequests({ commit }, repoUrl) {
    try {
      const [owner, repo] = repoUrl.split("/");
      const response = await octokitWrapper.paginate(octokitWrapper.octokit.rest.pulls.list, { owner, repo, state: "all", per_page: 100 });
      commit("SET_PRS", response);
    } catch (error) {
      console.error(error);
    }
  },

  getPaginatedPrs({ getters, commit }, pageNo = 1) {
    let start = (pageNo - 1) * getters.paginationLength;
    let end = Math.min(pageNo * getters.paginationLength, getters.prs.length);
    commit("SET_PAGINATED_PRS", getters.prs.slice(start, end));
  },

  async getRepoBranches({ commit }, repoUrl) {
    try {
      const response = await httpClient.get(`${VUE_APP_BASE_URL}:${VUE_APP_PORT}/github/repos/${repoUrl}/branches`);
      commit("SET_BRANCHES", response.data);
    } catch (error) {
      console.error(error);
    }
  },

  async compareTwoCommits(_, { repoUrl, base, compare, pageNo, pageSize }) {
    try {
      const response = await httpClient.get(`${VUE_APP_BASE_URL}:${VUE_APP_PORT}/github/repos/${repoUrl}/compare/${base}...${compare}`, { params: { page: pageNo, per_page: pageSize } });
      return response.data;
    } catch (error) {
      console.error(error);
    }
  },

  async createPullRequest(_, { repoUrl, title, head, base }) {
    try {
      const response = await httpClient.post(`${VUE_APP_BASE_URL}:${VUE_APP_PORT}/github/repos/${repoUrl}/pulls`, { title: title, head: head, base: base });
      return response.data.number;
    } catch (e) {
      if (e.data.errors?.[0]) throw e.data.errors[0];
      else throw { message: "Unknown error occurred" };
    }
  },

  async getPullRequestAllDetails({ dispatch }, { repoUrl, pullNumber }) {
    return dispatch("getPullRequest", {
      repoUrl,
      pullNumber,
      getReviewRequests: true,
      getViewerLatestPendingReviews: true,
      getViewerLatestReview: true,
      getLatestReviews: true,
      getTimelineItems: true,
      getReviewThreads: true,
    });
  },

  async getPullRequest({ getters }, { repoUrl, pullNumber, getReviewRequests, getViewerLatestPendingReviews, getViewerLatestReview, getLatestReviews, getTimelineItems, getReviewThreads }) {
    const [repoOwner, repoName] = repoUrl.split("/");
    const response = await octokitWrapper.graphql(GET_PULL_REQUEST, {
      repoOwner,
      repoName,
      pullNumber: Number(pullNumber),
      userLogin: getters.user.login,
      getReviewRequests,
      getViewerLatestPendingReviews,
      getViewerLatestReview,
      getLatestReviews,
      getTimelineItems,
      getReviewThreads,
    });
    const pullRequest = response.repository.pullRequest;
    await parsePullRequest(repoUrl, pullRequest, getReviewRequests, getViewerLatestPendingReviews, getViewerLatestReview, getLatestReviews, getTimelineItems, getReviewThreads);
  },

  async getPullRequestDiff(_, { repoUrl, pullNumber }) {
    try {
      const response = await httpClient.get(`${VUE_APP_BASE_URL}:${VUE_APP_PORT}/github/repos/${repoUrl}/pulls/${pullNumber}`, {
        headers: { Accept: "application/vnd.github.VERSION.diff" },
      });
      return response.data;
    } catch (error) {
      console.error(error);
    }
  },

  async getPullRequestInjectedBugs(_, { repoUrl, pullNumber }) {
    try {
      const response = await httpClient.get(`${VUE_APP_BASE_URL}:${VUE_APP_PORT}/repos/${repoUrl}/pulls/${pullNumber}/bugs`);
      return response.data;
    } catch (error) {
      console.error(error);
    }
  },

  async getReviewMarkedBugs(_, { repoUrl, pullNumber, reviewId }) {
    try {
      const response = await httpClient.get(`${VUE_APP_BASE_URL}:${VUE_APP_PORT}/repos/${repoUrl}/pulls/${pullNumber}/reviews/${reviewId}/marked-lines`);
      return response.data;
    } catch (error) {
      console.error(error);
    }
  },

  async injectManualBug(_, { repoUrl, pullNumber, lineNumber, buggyVersion, headRefOid }) {
    try {
      await httpClient.put(`${VUE_APP_BASE_URL}:${VUE_APP_PORT}/repos/${repoUrl}/pulls/${pullNumber}/bugs`, { lineNumber, buggyVersion, headRefOid });
    } catch (error) {
      console.error(error);
    }
  },

  async getRepoUsers({ commit }, repoUrl) {
    try {
      const [owner, name] = repoUrl.split("/");
      const response = await octokitWrapper.graphql(GET_ASSIGNABLE_REVIEWERS, { owner, name });
      const { nodes: users } = response.repository.assignableUsers;
      commit("SET_REPOUSERS", users);
    } catch (error) {
      console.error(error);
    }
  },

  async assignReviewer(_, { repoUrl, pullNumber, reviewer }) {
    try {
      await httpClient.post(`${VUE_APP_BASE_URL}:${VUE_APP_PORT}/github/repos/${repoUrl}/pulls/${pullNumber}/requested_reviewers`, { reviewers: [reviewer] });
    } catch (error) {
      console.error(error);
    }
  },

  async getBuggyDiff(_, { repoUrl, pullNumber }) {
    try {
      const response = await httpClient.get(`${VUE_APP_BASE_URL}:${VUE_APP_PORT}/repos/${repoUrl}/pulls/${pullNumber}/buggy-diff`);

      return response.data;
    } catch (error) {
      console.error(error);
    }
  },

  async createPullRequestReview({ commit }, { pullNumber, repoUrl, reviewAction }) {
    try {
      const response = await httpClient.post(`${VUE_APP_BASE_URL}:${VUE_APP_PORT}/github/repos/${repoUrl}/pulls/${pullNumber}/reviews`, {
        reviewAction,
      });
      commit("SET_REVIEW", response.data);
    } catch (error) {
      console.error(error);
    }
  },

  async markBug(_, { repoUrl, pullNumber, reviewNumber, lineNumber, mark }) {
    try {
      await httpClient.post(`${VUE_APP_BASE_URL}:${VUE_APP_PORT}/repos/${repoUrl}/pulls/${pullNumber}/reviews/${reviewNumber}/marked-lines`, { lineNumber: lineNumber, mark: mark });
    } catch (error) {
      console.error(error);
    }
  },

  async addPendingReviewComment({ getters }, { body, path, position, inReplyTo }) {
    const pullRequestReviewId = getters.review.id;
    try {
      await octokitWrapper.graphql(ADD_PULL_REQUEST_REVIEW_COMMENT, { body, path, position, pullRequestReviewId, inReplyTo });
    } catch (error) {
      console.error(error);
    }
  },

  async updateReviewComment(_, { id, body }) {
    try {
      await octokitWrapper.graphql(UPDATE_PULL_REQUEST_REVIEW_COMMENT, { id, body });
    } catch (error) {
      console.error(error);
    }
  },

  async deleteReviewComment(_, { id }) {
    try {
      await octokitWrapper.graphql(DELETE_PULL_REQUEST_REVIEW_COMMENT, { id });
    } catch (error) {
      console.error(error);
    }
  },

  async replySubmittedReviewComment(_, { repoUrl, pullNumber, commentId, body }) {
    try {
      await httpClient.post(`${VUE_APP_BASE_URL}:${VUE_APP_PORT}/github/repos/${repoUrl}/pulls/${pullNumber}/comments/${commentId}/replies`, { body: body });
    } catch (error) {
      console.error(error);
    }
  },

  async submitReview({ getters }, { repoUrl, pullNumber, reviewNumber, event }) {
    await httpClient.post(`${VUE_APP_BASE_URL}:${VUE_APP_PORT}/repos/${repoUrl}/pulls/${pullNumber}/reviews/${reviewNumber}/events`, {
      event,
      calculateScore: !getters.activePr.viewerCanSeeInjectedBugs,
    });
  },

  async createCommentForPR(_, { repoUrl, pullNumber, comment }) {
    try {
      await httpClient.post(`${VUE_APP_BASE_URL}:${VUE_APP_PORT}/github/repos/${repoUrl}/issues/${pullNumber}/comments`, { body: comment });
    } catch (error) {
      console.error(error);
    }
  },

  async updatePRComment(_, { id, body }) {
    try {
      await octokitWrapper.graphql(UPDATE_ISSUE_COMMENT, { id, body });
    } catch (error) {
      console.error(error);
    }
  },

  async deletePRComment(_, { id }) {
    try {
      await octokitWrapper.graphql(DELETE_ISSUE_COMMENT, { id });
    } catch (error) {
      console.error(error);
    }
  },

  async mergePullRequest(_, { repoUrl, pullNumber, title, commit_sha }) {
    try {
      await httpClient.put(`${VUE_APP_BASE_URL}:${VUE_APP_PORT}/github/repos/${repoUrl}/pulls/${pullNumber}/merge`, { title: title, sha: commit_sha });
    } catch (error) {
      console.error(error);
    }
  },

  async getPendingReviewScore(_, { repoUrl, pullNumber, reviewId }) {
    try {
      const response = await httpClient.get(`${VUE_APP_BASE_URL}:${VUE_APP_PORT}/repos/${repoUrl}/pulls/${pullNumber}/reviews/${reviewId}/calculate-accuracy`);
      return response.data;
    } catch (error) {
      console.error(error);
    }
  },

  async getReviewScores(_, { repoUrl, pullNumber, reviewIds }) {
    try {
      const response = await httpClient.post(`${VUE_APP_BASE_URL}:${VUE_APP_PORT}/repos/${repoUrl}/pulls/${pullNumber}/reviews`, { reviewIds });
      return response.data;
    } catch (error) {
      console.error(error);
    }
  },

  async getReviews(_, { repoUrl, pageNo, pageSize, sortBy, startDate, endDate, statusList, reviewerLoginList }) {
    try {
      const response = await httpClient.post(`${VUE_APP_BASE_URL}:${VUE_APP_PORT}/repos/${repoUrl}/reviews`, {
        pageNo,
        pageSize,
        sortBy,
        startDate,
        endDate,
        statusList,
        reviewerLoginList,
      });
      return response.data;
    } catch (error) {
      console.error(error);
    }
  },

  async getReviewStatsByReviewer(_, { repoUrl, filters }) {
    try {
      const response = await httpClient.post(`${VUE_APP_BASE_URL}:${VUE_APP_PORT}/repos/${repoUrl}/reviews/by-reviewer`, filters);
      return response.data.map((obj) => {
        obj.averageAccuracy = Math.round(obj.averageAccuracy * 100) / 100;
        return obj;
      });
    } catch (error) {
      console.error(error);
    }
  },

  async getReviewHistogram(_, { repoUrl, filters }) {
    try {
      const response = await httpClient.post(`${VUE_APP_BASE_URL}:${VUE_APP_PORT}/repos/${repoUrl}/reviews/histogram`, filters);
      if (response.data.length > 0) {
        const analyzedCounts = [];
        const faultyCounts = [];
        const totalCounts = [];
        const averageAccuracies = [];
        let epochDate;
        let faultySum = 0;
        let analyzedSum = 0;
        let totalSum = 0;
        let averageAccuracySum = 0;
        response.data.forEach((obj) => {
          epochDate = new Date(obj._id).getTime();
          analyzedCounts.push([epochDate, obj.analyzedCount]);
          faultyCounts.push([epochDate, obj.faultyCount]);
          totalCounts.push([epochDate, obj.totalCount]);
          averageAccuracies.push([epochDate, Math.round(obj.averageAccuracy * 100) / 100]);
          faultySum += obj.faultyCount;
          analyzedSum += obj.analyzedCount;
          totalSum += obj.totalCount;
          averageAccuracySum += obj.averageAccuracy * obj.analyzedCount;
        });
        return {
          totalFaulty: faultySum,
          totalSuccessful: analyzedSum - faultySum,
          analyzedDataSet: analyzedCounts,
          faultyDataSet: faultyCounts,
          totalDataSet: totalCounts,
          accuracyDataSet: averageAccuracies,
          totalReviews: totalSum,
          averageAccuracy: analyzedSum === 0 ? 0 : averageAccuracySum / analyzedSum,
        };
      }
      return false;
    } catch (error) {
      console.error(error);
    }
  },

  async getRepoConfig({ commit }, { repoUrl }) {
    try {
      const { data } = await httpClient.get(`${VUE_APP_BASE_URL}:${VUE_APP_PORT}/repos/${repoUrl}/config`);
      commit("SET_REPO_CONFIG", data);
    } catch (error) {
      console.error(error);
    }
  },

  async updateRepoConfig({ commit }, { repoUrl, config }) {
    try {
      const { data } = await httpClient.put(`${VUE_APP_BASE_URL}:${VUE_APP_PORT}/repos/${repoUrl}/config`, config);
      commit("SET_REPO_CONFIG", data);
    } catch (error) {
      console.error(error);
    }
  },

  async logout({ commit }) {
    await router.push("/");
    commit("SET_DEFAULT_STATE");
  },
};
